import * as Ons from 'react-onsenui';
import * as React from 'react';

export class TestPage extends React.Component {
  _renderTabs() {
    return [
    { 'tab': <Ons.Tab></Ons.Tab>, 'content': <div>Hi</div> }
    ];
  }

  render() {
    return (
      <Ons.Page>
        <Ons.Tabbar index={2} renderTabs={this._renderTabs} />
      </Ons.Page>
    );
  }
}


